import React,{useState, useEffect} from 'react';
import './App.css';
import Form from './components/Form';
import TodoList from './components/TodoList'

function App() {

  const [input,setInput]=useState('');
    const [items,setItems]=useState([]);
    const [status,setStatus]=useState('all');
    const [filteredItems,setFilteredItems]=useState([]);
    //Run once only
    useEffect(()=>{
      getLocalTodos();
    },[])
    //
    useEffect(()=>{
           filterHandler();
           saveLocalTodos();
    },[items,status]);
 const filterHandler=()=>{
     switch(status){
       case 'completed':
         setFilteredItems(items.filter(item=>item.completed===true));
         break;
           case 'uncompleted':
         setFilteredItems(items.filter(item=>item.completed===false));
         break;
         default:
           setFilteredItems(items);
           break;
     }
    }
    const saveLocalTodos=()=>{
        localStorage.setItem("items",JSON.stringify(items));
    }
     const getLocalTodos=()=>{
      if(localStorage.getItem("items")===null){
        localStorage.setItem("items",JSON.stringify([]));
      }
      else{
       let todoLocal =JSON.parse(localStorage.getItem("items",JSON.stringify(items)));
       setItems(todoLocal);
      }
    }
  return (
    <>
    <div className="App">
      <header>
         <h1>Todo List</h1> 
         </header>
         <Form input={input} 
         setInput={setInput} 
         items={items}
          setItems={setItems}
          setStatus={setStatus}
          />
         <TodoList items={items}  filteredItems={filteredItems} setItems={setItems} />
    </div>
    </>
  );
}

export default App;
